import React, { createElement, useEffect, useState, useRef, useMemo, createRef } from 'react'
import { useMeasure, useWindowScroll, useWindowSize, useIntersection } from 'react-use'
import { useDisclosure, Icon, IconButton } from "@chakra-ui/react"
import { graphql, useStaticQuery, withPrefix } from 'gatsby'
import loadable from '@loadable/component'
import { HiOutlineChevronDoubleDown } from "react-icons/hi";
import { IoIosArrowUp } from "react-icons/io";
import { keyframes } from '@emotion/react';
import styled from '@emotion/styled'
import { HideDuring } from 'react-hide-on-scroll'

import Container from '../../components/Container'
import Text from '../../components/Text'
import Box from '../../components/Box'
import { responsive } from '../../components/ThemeProvider/theme';

import Layout from '../Layout';

import Banner from './Banner'
import BottomNav from './BottomNav'
import { trackClick, trackView } from '../../utils/trackEvent'

// import VideoModal from '../VideoModal'
import FocusOne from './FocusOne'
import FocusTwo from './FoucusTwo'
import NewMarket from './NewMarket'
import Esg from './Esg'
// import KeyPoint from './KeyPoint'
import Fund from './Fund'
import Buy from './Buy'

const VideoModal = loadable(() => import('../VideoModal'))

const pages = [
  // 'section-focus',
  'ESG-green',
  'new-market',
  'target',
  'section-subject',
  'section-fund',
  'section-buy'
]

const titles = [
  // '',
  'ESG',
  '',
  '',
  'ESG',
  '',
  '',
].reduce((obj, n, i) => {
  obj[pages[i]] = n
  return obj
}, {})

const comps = [
  // KeyPoint,
  FocusOne,
  FocusTwo,
  NewMarket,
  Esg,
  Fund,
  Buy,
]

const anime = keyframes`
  0%,,
  100% {
    transform: translateY(15%);
  }
  50% {
    transform: translateY(-15%);
  }
`

const DownAnimation = styled(Box)`
  animation: ${anime} 1s infinite;
`

const IndexPage = () => {
  const { site: { siteMetadata } } = useStaticQuery(graphql`
    query HomeQuery {
      site {
        siteMetadata {
          title
          description
          url
        }
      }
    }
  `)
  const [ref, { height }] = useMeasure();
  const [page, setPage] = useState()
  const [pin, setPin] = useState(true)
  const footerRef = useRef()
  const intersection = useIntersection(footerRef, {
    root: null,
    rootMargin: '0px',
    threshold: 0.1,
  });
  // const [bannerLoaded, setBannerLoaded] = useState()
  // const [inverse, setInverse] = useState()
  const { isOpen, onOpen, onClose } = useDisclosure()
  const { y } = useWindowScroll();
  const pageRefs = useMemo(() => pages.map(() => createRef()), [])
  useEffect(() => {
    if (process.env.NODE_ENV === 'production') {
      window.history.replaceState(null, null, withPrefix(`/index.html${window.location.search}`))
    }
    const options = {
      rootMargin: '0px',
      threshold: 0.1,
    }

    pageRefs.forEach((pageRef, i) => {
      const name = pages[i]
      const observer = new IntersectionObserver(e => {
        if (e && e[0]) {
          if (e[0].isIntersecting) {
            setPage(name)
            window.history.replaceState(null, null, `${window.location.pathname}#${name}`)
          }
        }
      }, options);
      observer.observe(pageRef.current)
    })
  }, [])

  useEffect(() => {
    if (page) {
      trackView(page, `${titles[page]} | ${siteMetadata.title}`)
    }
  }, [page, siteMetadata])
  const { width, height: windowHeight } = useWindowSize()
  const isFatScreen = useMemo(() => width > windowHeight, [windowHeight, width])
  return (
    <Layout onOpen={onOpen} siteMetadata={siteMetadata}>
      {/* <Box.Absolute top="-9999px" left="-9999px" fontSize="0" zIndex={-999} opacity="0" visibility="none">
        {pages.map((name) => (
          <Link
            spy
            to={name}
            onSetActive={(to) => setPage(to)}
            key={name}
            hashSpy
          />
        ))}
      </Box.Absolute> */}
      <Box
        as="section"
        id="intro"
        height={responsive(`calc(100% - ${height}px)`, isFatScreen ? 'auto' : '100%', 'auto')}
      >
        <Banner isFatScreen={isFatScreen} />
      </Box>
      {comps.map((p, i) => {
        const show = page === pages[i]
        return (
          <section id={pages[i]} key={i} ref={pageRefs[i]}>
            {createElement(p, { show })}
          </section>
        )
      })}
      <Container ref={footerRef} textAlign="justify" fontWeight="bold" py="3em" fontSize="0.875em">
         |()
        <Text.Inline color="custom.orange"></Text.Inline>
        
        <Text.Inline color="custom.orange">Rule 144A(CDSCDX indexiTraxx Index)</Text.Inline>
        <Text.Inline color="custom.orange">N</Text.Inline>
      </Container>
      <BottomNav setPin={setPin} onOpen={onOpen} ref={ref} />
      <VideoModal onClose={onClose} isOpen={isOpen} />
      {!intersection?.isIntersecting && createElement(y ? HideDuring : Box, { inverse: true }, (
        <Box
          position="fixed"
          left="50%"
          transform="translateX(-50%)"
          bottom={responsive(`calc(${`${pin ? height : 0}px`} + 1em)`, '3em')}
          color={y ? 'custom.textSource' : "white"}
        >
          <DownAnimation>
            <Icon width={responsive('2em', '2.5em')} height={responsive('2em', '2.5em')} as={HiOutlineChevronDoubleDown} />
          </DownAnimation>
        </Box>
      ))}
      {Boolean(y) && (
        <Box
          zIndex={99}
          position="fixed"
          cursor="pointer"
          color="white"
          right="1.25em"
          bottom={responsive('8em', '10.25em')}
          // transform={responsive(`translateY(${pin ? -height : 0}px)`, 'none')}
          transition="transform 200ms ease-in-out"
        >
          <IconButton
            onClick={() => {
              trackClick('BackToTop')
              window.scrollTo({ left: 0, top: 0, behavior: 'smooth' })
            }}
            size="sm"
            bg="custom.blue"
            _hover={{ bg: 'custom.blue' }}
            icon={<IoIosArrowUp />}
          />
        </Box>
      )}
    </Layout>
  )
}

export default IndexPage
